.hero{
	h1{
		margin-block-start: 0em;
		margin-block-end: 0em;
		font-size: 26px;
		color: #fff;
		text-align: center;
	}
	p{
		font-family: 'Open Sans Condensed', sans-serif;
		text-transform: uppercase;
		margin-top: -50px;
		font-weight: 300;
		font-size: 14px;
		color: #fff;
		text-align: center;
	}
	.hero__statement{
		font-family: 'Open Sans Condensed', sans-serif;
		color: #fff;
		padding: 15px;
		background-color: rgba(251, 193, 5, 0.78);
		text-align: center;
		font-size: 20px;
		text-transform: uppercase;
		font-weight: bold;
	}
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 100vw;
	height: 300px;
	margin-left: -10px;
	margin-right: -10px;
	padding: 10px;
	padding-top: 30px;
	background-image: url(/images/hero-pic1-min.jpg);
	background-repeat: no-repeat;
}
